.loopring--news {
	padding-top: 78px;
	padding-bottom: 60px;
	min-height: 100vh;
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(24, 55, 238, 0.8)), to(rgba(24, 55, 232, 0.8))),
		url('../../../img/bgs/congruent_pentagon.png');
	background: -webkit-linear-gradient(rgba(24, 55, 238, 0.8), rgba(24, 55, 232, 0.8)),
		url('../../../img/bgs/congruent_pentagon.png');
	background: -o-linear-gradient(rgba(24, 55, 238, 0.8), rgba(24, 55, 232, 0.8)),
		url('../../../img/bgs/congruent_pentagon.png');
	background: linear-gradient(rgba(24, 55, 238, 0.8), rgba(24, 55, 232, 0.8)),
		url('../../../img/bgs/congruent_pentagon.png');
}

.loopring--news--container {
	width: 80%;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	color: #fff;
}

.loopring--news--container h1 {
	margin: 40px 0;
}

.loopring--news--inner--container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	width: 100%;
	-ms-flex-pack: center;
	    justify-content: center;
}

.@media(max-width: 600px) {
	.loopring--news {
		padding-top: 52px;
	}
}



