.person--list--item {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	width: 100%;
	padding: 8px 15px;
	cursor: pointer;
}

.person--list--item--container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
}

.person--list--item--container i {
	margin-left: auto;
}

.person--list--item img {
	height: 40px;
	width: 40px;
	border-radius: 100%;
	margin-right: 10px;
}

.person--list--item--credentials {
	width: 100%;
	padding: 15px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	visibility: hidden;
	position: fixed;
	opacity: 0;
}

.person--list--item--credentials--header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	color: #585858;
}

.person--list--item--credentials--header h4 {
	margin: 0;
	margin-left: 10px;
}

.person--list--item--credentials ul {
	margin-top: 8px;
}

.person--list--item--credentials ul li {
	color: #888;
}

.opacity {
	opacity: 1;
}

.transition {
	-webkit-transition: all 0.4s ease;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
}



