.loopring--faq {
	padding-top: 78px;
	background-color: #f7f7f7;
}

.loopring--faq--container {
	width: 80%;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	padding: 60px 0;
}

.loopring--faq--inner--container {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.loopring--faq--inner--container .loopring--faq--question:first-child {
	border-top: none;
}

.loopring--faq--inner--container .loopring--faq--question:last-child {
	border-bottom: none;
}

@media (max-width: 600px) {
	.loopring--faq {
		padding-top: 52px;
	}
}




